<template>
  <div>
    <van-nav-bar
      :fixed="true"
      title="运维管理"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    ></van-nav-bar>
    <div class="body">
      <!-- <div @click="toNavigate('dayControlMaintain')">日常运维</div>
      <div @click="toNavigate('emergencyMaintain')">应急运维</div>-->

      <div class="naviBar">
        <div class="flexBox"  @click="toNavigate('dayControlMaintain')">
          <div class="imageSize">
            <img src="../../../static/controlNavi/daily.png" alt />
          </div>
          <div>
            <span>日常运维</span>
          </div>
        </div>
        <div class="flexBox">
          <div class="imageSize" @click="toNavigate('emergencyMaintain')">
            <img src="../../../static/controlNavi/emergency.png" alt />
          </div>
          <div>
            <span>应急运维</span>
          </div>
        </div>
      </div>
      <control-navi-component></control-navi-component>
    </div>
  </div>
</template>
<script>
import ControlNaviComponent from "./naviContentComponent";
export default {
  data() {
    return {};
  },
  components: {
    ControlNaviComponent
  },
  methods: {
    toNavigate(nav) {
      this.$router.push(nav);
    }
  },
  created() {},
  mounted() {},
  destroyed() {}
};
</script>
<style scoped>
.body {
  margin-top: 46px;
  /* background-color: red; */
}
.naviBar {
  justify-content: space-between;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 10px;
}
.flexBox {
  width: 49%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
  border-radius: 10px;
}
.imageSize img {
  width: 100px;
  height: 100px;
}
</style>


