<template>
  <div>
    <div class="containerBox" v-for="(item,index) in 9" :key="index">
      <div class="flexBox">
        <div class="headerContent borderStyle" >
          <div class="imageSize">
            <img src="../../../static/controlNavi/station.png" alt />
          </div>
          <div class="headerFont">水站名称</div>
        </div>
        <div class="flexDerection">
          <div class="boxContent">
            <div class="flexCenter">
              <img src="../../../static/controlNavi/huanbao.png" alt />
            </div>
            <span class="margin-right">废液</span>
            <span>2020.04.07</span>
          </div>
          <div class="boxContent">
            <div class="flexCenter">
              <img src="../../../static/controlNavi/Testing.png" alt />
            </div>
            <span class="margin-right">试剂</span>
            <span>2020.04.07</span>
          </div>
          <div class="boxContent">
            <div class="flexCenter">
              <img src="../../../static/controlNavi/water.png" alt />
            </div>
            <span class="margin-right">纯水</span>
            <span>2020.04.07</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: [],
  methods: {},
  created() {}
};
</script>
<style scoped>
.flexBox {
  background-color: white;
  /* padding-left: 20px; */
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 10px;
  color: #999999;
}
.flexDerection {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.boxContent {
  width: 50%;
  height: 40px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 10px;
}
.flexCenter {
  display: flex;
  align-items: center;
  margin-right: 5px;
}
.margin-right {
  margin-right: 5px;
}
/* 头部 */
.headerContent {
  color: #464646;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 10px;
  padding-left: 10px;
  font-family: SourceHanSansCN-Regular;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0px;
  color: #464646;
}
.headerFont {
  font-family: SourceHanSansCN-Regular;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  line-height: 22px;
  letter-spacing: 0px;
  color: #464646;
}
.imageSize {
  /* width: 32px;
    height: 32px; */
  display: flex;
  align-items: center;
}
.containerBox {
  padding: 0 10px;
  margin-bottom: 10px;
}
</style>


